import clien_logo from './clien.png';
import top_ad from './top_ad.png';
import side_ad from './side.png';
import main_ad from './main_ad.png';
import './App.css';
import recommand_data from './recommand';
import information_data from './information';
import sell_data from './sell';
import hongbo_data from './hongbo';
import use_data from './use';
import { useState } from 'react';

const { Kakao } = window;

function App() {
  let recommandArray = recommand_data;
  let informationArray = information_data;
  let useArray = use_data;
  let sellArray = sell_data;
  let hongboArray = hongbo_data;

  let [name, nameChange] = useState('비회원');
  let [thumbnail, thumbnailChange] = useState('');
  let [showLogin, showLoginChange] = useState(true);


  function KaKaoLoginGo() {
    return (
      Kakao.Auth.loginForm({
        success: function (authObj) {
          showResult(JSON.stringify(authObj))

          Kakao.API.request({
            url: '/v2/user/me',
            data: {
              property_keys: ["kakao_account.email", "kakao_account.gender", "properties.nickname", "properties.thumbnail_image"]
            },
            success: function (response) {
              alert("success : " + response.properties.nickname);
              console.log(response);
              nameChange(response.properties.nickname);
              thumbnailChange(response.properties.thumbnail_image);
              showLoginChange(false);
            },
            fail: function (error) {
              alert("fail : " + error);
              console.log(error);
            }
          });
        },
        fail: function (err) {
          showResult(JSON.stringify(err))
        },
      })
    )
  }


  function showResult(result) {
    console.log("result : " + result);
    //alert(result);
  }

  function KaKaoLogoutGo() {
    if (!Kakao.Auth.getAccessToken()) {
      alert('로그인 되어 있지 않습니다.')
      return
    }
    Kakao.Auth.logout(function () {
      alert('로그아웃이 성공했습니다. \n\n access token -> ' + Kakao.Auth.getAccessToken());
      nameChange('비회원');
      thumbnailChange('');
      showLoginChange(true);
    })
  }

  function KaKaoUnlink() {
    Kakao.API.request({
      url: '/v1/user/unlink',
      success: function (res) {
        alert('앱탈퇴가 성공했습니다. \nmsg : ' + JSON.stringify(res));
        nameChange('비회원');
        thumbnailChange('');
        showLoginChange(true);
      },
      fail: function (err) {
        alert('앱탈퇴가 실패했습니다. \nmsg : ' + JSON.stringify(err))
      },
    })
  }

  function handleKakaoSuccess() {
    alert('성공');
  }

  function handleKakaoFail() {
    alert('실패');
  }

  function LoginButton() {
    return (
      <div>
        {
          showLogin
            ? <button type="button" onClick={KaKaoLoginGo} name="로그인하기" className="button_submit">로그인</button>
            : null
        }
      </div>
    )
  }

  function LogoutButton() {
    return (
      <div>
        {
          !showLogin
            ? <div className="account_signup">
              <a href="#" class="button_find" onClick={KaKaoLogoutGo}>로그아웃</a>
              <a href="#" class="button_signup" onClick={KaKaoUnlink}><i class="fa fa-user-circle-o"></i> 앱 탈퇴</a>
            </div>
            : null
        }
      </div>
    )
  }


  function Aside(props) {
    return (
      <div className="aside">
        <div className="side_account">
          <form id="loginForm">
            {/* <input type="text" autoCapitalize="off" placeholder="아이디" className="input_id" display="none" />
          <input type="password" placeholder="비밀번호" className="input_pw" /> */}
            <h3 id="nickname">{props.name}</h3>
            <ThumbNail></ThumbNail>
            <div className="account_submit">
              {/* <button type="button" onClick={KaKaoLoginGo} name="로그인하기" className="button_submit">로그인</button> */}
              <LoginButton></LoginButton>
            </div>

            {/* <div className="account_signup">
              <a href="#" class="button_find" onClick={KaKaoLogoutGo}>로그아웃</a>
              <a href="#" class="button_signup" onClick={KaKaoUnlink}><i class="fa fa-user-circle-o"></i> 앱 탈퇴</a>
            </div> */}
            <LogoutButton></LogoutButton>
          </form>

        </div>

        <img src={side_ad} />
      </div>
    )
  }

  function ThumbNail() {
    return (
      <div>
        {
          !thumbnail
            ? <img width="100px" height="auto" src="https://mblogthumb-phinf.pstatic.net/MjAyMDAzMDdfMTkz/MDAxNTgzNTcwNDY4NDE4.pX2NNnh0WmJDRnWrDK69zjSZvACUPm3kyE3_2AJ_K5Yg.y1vbIqEQCkNF1PCXi90YfjOfslH36xf_faV_u39Mw0Ag.JPEG.bmybmybmy/IMG_8267.JPG?type=w800" />
            : <img src={thumbnail} />
        }
      </div>
    )
  }

  return (
    <div className="App">
      <body>
        <nav className="nav_wrapper">
          <div className="nav_header">
            <a href="#" className="logo"><img src={clien_logo} className="clien_img" onClick={() => alert('welcom clien best')} /></a>
            <div className="title">
              톺아보기
            </div>
            <div className="title">
              공감글
            </div>
            <div className="title">
              추천글
            </div>
            <input className="top_search" type="search" title="통합검색" placeholder="검색어를 입력하세요" />
          </div>
        </nav>

        <div className="nav_container">
          <Snb></Snb>
          <Main recommand={recommandArray} information={informationArray} use={useArray} sell={sellArray} hongbo={hongboArray}></Main>
          <Aside name={name}></Aside>
        </div>

        <div className="footer_wrap">
          <div class="area_terms">
            <a class="at_item" href="/service/board/rule">이용규칙</a>
            <a class="at_item" href="/service/board/annonce">운영알림판</a>
            <a class="at_item black" href="/service/board/symposium">운영소통</a>
            <a class="at_item" href="/service/board/claim">재검토요청</a>
            <a class="at_item" href="/service/board/faq">도움말</a>
            <a class="at_item noline" href="/service/board/bug">버그신고</a>
            <div class="button_cs">
              <a href="javascript:;" class="button_dropmenu" data-toggle="dropdown">고객지원</a>
              <ul class="dropdown-menu up">
                <li><a href="/service/cs/singo/article">게시물 삭제 요청</a></li>
                <li><a href="/service/cs/singo/message">쪽지 신고</a></li>
                <li><a href="/service/cs/singo/nickname">닉네임 신고</a></li>
                <li><a href="mailto:admin@clien.net">제보 및 기타 제안</a></li>
              </ul>
            </div>
          </div>

          <div class="area_legal">
            <a href="/service/cs/privacy" target="_blank">개인정보처리방침</a>
            <a href="/service/cs/conditions" target="_blank">이용약관</a>
            <a href="/service/cs/policy" target="_blank">책임의 한계와 법적고지</a>
            <a href="/service/cs/teenager" target="_blank">청소년 보호정책</a>
          </div>

          <div class="area_company">© <span id="nowYear">2021</span>&nbsp;&nbsp;•&nbsp;&nbsp;CLIEN BEST.NET</div>
        </div>
      </body>
    </div>
  );
}

